import React from 'react';

const languages = [
  { code: 'English', name: 'English', flag: '' },
  { code: 'Hindi', name: ' (Hindi)', flag: '' },
  { code: 'Spanish', name: 'Espaol (Spanish)', flag: '' },
  { code: 'French', name: 'Franais (French)', flag: '' },
  { code: 'German', name: 'Deutsch (German)', flag: '' },
  { code: 'Chinese', name: ' (Chinese)', flag: '' },
  { code: 'Japanese', name: ' (Japanese)', flag: '' },
  { code: 'Korean', name: ' (Korean)', flag: '' },
];

const LanguageSelection = ({ currentLanguage, setLanguage, theme }) => {
  const isDark = theme === 'dark';

  return (
    <div className="settings-container" style={{ 
      backgroundColor: isDark ? '#0f172a' : '#f8fafc',
      minHeight: '100vh',
      padding: '20px'
    }}>
      <h2 style={{ 
        color: isDark ? '#f1f5f9' : '#1e293b',
        marginBottom: '24px'
      }}>
        Select Language /  
      </h2>
      
      <div className="settings-list">
        {languages.map((lang) => (
          <div
            key={lang.code}
            className={`settings-item ${currentLanguage === lang.code ? 'active' : ''}`}
            onClick={() => setLanguage(lang.code)}
            style={{
              backgroundColor: currentLanguage === lang.code 
                ? (isDark ? '#1e293b' : '#e0e7ff') 
                : (isDark ? '#1e293b' : '#fff'),
              border: currentLanguage === lang.code 
                ? '2px solid #6366f1' 
                : '2px solid transparent',
              cursor: 'pointer',
              transition: 'all 0.2s ease'
            }}
          >
            <span style={{ fontSize: '24px', marginRight: '12px' }}>{lang.flag}</span>
            <span style={{ 
              color: isDark ? '#f1f5f9' : '#1e293b',
              fontWeight: currentLanguage === lang.code ? '600' : '400'
            }}>
              {lang.name}
            </span>
            {currentLanguage === lang.code && (
              <span style={{ 
                marginLeft: 'auto', 
                color: '#6366f1',
                fontSize: '20px'
              }}>
                
              </span>
            )}
          </div>
        ))}
      </div>
      
      <p style={{ 
        color: isDark ? '#94a3b8' : '#64748b',
        marginTop: '24px',
        textAlign: 'center'
      }}>
        More languages coming soon!
      </p>
    </div>
  );
};

export default LanguageSelection;
